import React from 'react';
import './App.scss';
import { BrowserRouter as Router, Route, Redirect, Switch } from 'react-router-dom';
import { Root } from 'react-static';

import Home from './pages/Home';
import Projects from './pages/Projects';
import Portfolio from './pages/Portfolio';
import Articles from './pages/Articles';
import Music from './pages/Music';
import Article from './Articles/article';
import About from './pages/About';
import NotFound from './pages/NotFound';

import Title from './common/title';
import Navigation from './common/navigation';
import Social from './common/social';

import axios from './const/axios';
import { ArticleData, ArticleDataDictionary } from './interfaces';

const regex = /(?:\w|\s)+/g;

export default class App extends React.Component<{}, {
	articles: ArticleDataDictionary
}> {

	public state = {
		articles: {} as ArticleDataDictionary
	}
	
	static async getArticles() {
		try {
			let { data } = await axios('/articles.json');
			let articles = {} as ArticleDataDictionary;
			Object.entries(data.articles as {[key: string]: ArticleData[]}).map(([sectionKey, v]) => {
				v.forEach((metadata: ArticleData) => {
					let {name, subtitle, dateCreated} = metadata;
					let date = new Date(dateCreated);
					if (isNaN(date.getTime())) return;
					let year = date.getFullYear();
					let month = date.getMonth();
					let day = date.getDay();
					let urlEncoded = (name.toLowerCase().match(regex) || []).join('').split(' ').join('-');
					let section = data.keys[sectionKey];
					let href = `/articles/${sectionKey}/${year}/${month}/${day}/${urlEncoded}`;
					let key = process.env.PUBLIC_URL + href;
					articles[key] = Object.assign(metadata, {
						year, month, day, href, section, sectionKey, subtitle
					});
				});
				return articles;
			})
			return articles;
		} catch(e) {
			console.error(e);
		}
	}

	async componentDidMount() {
		let articles = await App.getArticles() as ArticleDataDictionary;
		this.setState({articles});
	}

	render() {
		return (
			<div className='container'>
				<Root>					
					<Router basename={process.env.PUBLIC_URL}>
						<Title />
						<Navigation />
						<div className='feed'>
							<Switch>
								<Route exact path='/' component={Home} />
								<Route exact path='/projects' component={Projects} />
								<Route exact path='/portfolio' component={Portfolio} />
								<Route exact path='/music' component={Music} />
								<Route exact path='/articles' render={() => <Articles articles={this.state.articles} />} />
								<Route exact path='/articles/:section/:year/:month/:date/:title' render={(props) => <Article window={props} articles={this.state.articles} />} />
								<Route exact path='/about' component={About} />
								<Route exact path='/home' component={() => <Redirect to='/' />} />
								<Route path='*' component={NotFound} status={404} />
							</Switch>
						</div>
						<Social />
					</Router>
				</Root>
			</div>
		);
	}
}