import React, { useState, useCallback } from 'react';
import Lightbox, { Modal, ModalGateway } from 'react-images';
import path from 'path';
import { ImageProps } from '../interfaces';

export function buildImage(gallery: ImageProps, i: number, id: string): ImageProps {
	return {
		key: id + '.' + i.toString() + '.Image',
		alt: gallery.path.toLowerCase().replace(/\s+/g, '_').replace('.jpg', ''),
		src: path.join(process.env.PUBLIC_URL, 'gallery', id, i.toString(), gallery.path),
		style: Object.assign({}, gallery, {
			type: undefined,
			path: undefined
		}, window.innerWidth < 480 ? {minWidth: '100%'} : null),
		index: 0,
		path: gallery.path
	};
}

export default function ImageContainer(props: ImageProps) {
	const [currentImage, setCurrentImage] = useState(0);
	const [viewerIsOpen, setViewerIsOpen] = useState(false);

	const openLightbox = useCallback((event, { photo, index }) => {
		setCurrentImage(index);
		setViewerIsOpen(true);
	}, []);

	const closeLightbox = () => {
		setCurrentImage(0);
		setViewerIsOpen(false);
	};
	return (
		<div className='DisplayImage'>
			<img
				{...props}
				alt={props.alt}
				onClick={() => openLightbox('click', props)}
			/>
			{props.enableLightbox !== false ? (
				<ModalGateway>
					{viewerIsOpen ? (
						<Modal onClose={closeLightbox}>
							<Lightbox
								currentImage={currentImage}
								onClose={closeLightbox}
								views={[props]}
							/>
						</Modal>
					) : null}
				</ModalGateway>
			) : null}
		</div>
	);
}