import React, { ReactElement } from 'react';
import { Link } from 'react-router-dom';
import Markdown from 'react-markdown';

import { ArticleDataDictionary } from '../interfaces';
import styles from '../assets/styles/article.module.css';

export default class Articles extends React.Component<{
	articles: ArticleDataDictionary
}> {

	render(): ReactElement {
		let obj = {} as {[key: string]: ReactElement[]};
		Object.entries(this.props.articles).forEach(([k, {id, name, href, section, dateCreated}]) => {
			if (k === 'window') return null;
			if (!obj[section]) obj[section] = [];
			let date = new Date(dateCreated);
			if (isNaN(date.getTime())) return null;
			obj[section].push(
				<li key={id}>
					<Link to={href}>
						<h3>{<Markdown source={name} />}</h3>
						<h4>{dateCreated}</h4>
					</Link>
				</li>
			)
		});
		return (
			<div className={styles.articles}>
				{Object.entries(obj).map(([k, v]) => {
					return (
						<div key={k} id={k}>
							<h1>{k}</h1>
							<ul>{v}</ul>
						</div>
					)
				})}
			</div>
		);
	}
}